@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;600&display=swap");

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    min-height: 100vh;
    font-family: "Source Sans Pro", sans-serif;
    text-align: center;
}

.header h1 {
    padding: 40px 0 30px;
    color: #333;
    font-size: 2.5rem;
    font-weight: 600;
}

.app form {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

.app form input,
.app form select {
    min-width: 250px;
    max-width: 100vw;
    font-size: 15px;
    padding: 5px;
    border: none;
    border-bottom: 1px solid #808080;
}

.app form input:focus,
.app form select:focus {
    outline: 1px solid #0080ff;
    border-bottom: none;
    border-radius: 4px;
}

.app button {
    background-color: #0080ff;
    color: #fff;
    padding: 10px 15px;
    margin: 0 10px;
    border: none;
    border-radius: 5px;
    transition: 0.5s ease-out;
}

.app button:hover {
    transform: scale(1.05);
    opacity: 0.8;
    cursor: pointer;
}

.app form select {
    min-width: 100px;
}

.recipeCard {
    display: inline-block;
    margin: 15px;
    border: none;
    border-radius: 5px;
    box-shadow: 3px 5px 10px #151515;
    transition: 1s ease-out;
}
.recipeCard:hover {
    transform: scale(1.05);
    opacity: 0.8;
    cursor: pointer;
}
.recipeCard p {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    width: 200px;
    margin: auto;
    padding: 10px;
}
.recipeCard img {
    width: 250px;
    height: 250px;
    object-fit: cover;
    margin-bottom: -4px;
}

@media (max-width: 560px) {
    .recipeCard img {
        width: 300px;
        height: 300px;
    }
    .app form {
        flex-direction: column;
    }
    .app form input,
    .app form select {
        width: 80vw;
        margin: auto;
    }
    .app form button {
        margin: 15px auto;
    }
    .app form input:focus,
    .app form select:focus {
        outline: none;
        border-radius: 0;
        border-bottom: 1px solid #0080ff;
    }
}
